.progress-dashboard {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
  }
  
  .progress-dashboard h2 {
    text-align: center;
    color: #333;
    margin-bottom: 30px;
  }
  
  .weekly-progress {
    margin-top: 30px;
    padding: 20px;
    background-color: #f8f9fa;
    border-radius: 8px;
  }
  
  .weekly-progress h3 {
    margin-top: 0;
    color: #555;
  }
  
  .weekly-bars {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  
  .week-item {
    display: flex;
    align-items: center;
    gap: 15px;
  }
  
  .week-label {
    min-width: 80px;
    font-size: 14px;
    color: #666;
  }
  
  .week-percentage {
    min-width: 40px;
    font-size: 14px;
    color: #333;
    font-weight: bold;
  }